/**
 * Font styles
 */

$primary-font-stack: 'SF-UI-Text-Regular';
$primary-font-default: 'Helvetica Neue, Helvetica, Arial, sans-serif';
$material-icons-font-size: 22px;
$font-size-xl: 40px;
$font-size-xs: 36px;
$font-size-ll: 32px;
$font-size-ls: 24px;
$font-size-ml: 18px;
$font-size-ms: 14px;
$font-size-sl: 12px;
