$icon-black: #000;
$icon-white: #fff;
$icon-green: #7ed321;
$icon-orange: #F5A623;


/**
 * Badge styles
 */

$badge-blue:#d7e8fb;
$badge-green:#cfeed4;
$badge-red:#fde8ea;
$badge-yellow:#fdf8ba;


/**
 * Text color styles
 */

$text-red : #ff0500;

/**
 * Width styles
 */

$container-max-width: 1760px;
$width-xs: 220px;
